var dungeonsList=[{name:'Black Rook Hold',imgUrl:'brh.jpg',bossCount:4,bosses:[{name:'Amalgam of Souls',imgUrl:'amalgam.png',strategy:["Tank the boss in one of the corners, and face him away from the group.","DPS and healer should stand behind the boss to easily avoid reap soul.","If targeted by swirling scythe, move away from the area.","If targeted by soul echoes, keep moving, and try not to drop it on other players' locations.","When the Amalgam of Souls begins to cast Call Souls, kill as many Restless Souls as possible before they reach the boss.","Restless Souls are not immuned to crowd control effects.","On higher keys and if tyrannical affix is active, players will need to use a defensive cooldown to survive soul burst."]},{name:'Illysanna Ravencrest',imgUrl:'illysanna.png',strategy:["Brutal Glaive bounces to other players if they are too close to each other, it is therefore recommended to spread out for this fight.","Players targeted by Dark Rush should spread out along one side of the room (agreed upon before starting the encounter) to maximize available space and minimize damage.","Tanks should use active mitigation to prevent Vengeful Shear from stacking up too much.","In phase two, the player targeted by Eye Beams, should kite in such a way as to minimize the amount of fire.","Illysanna summons a Risen Arcanist and a Soul-Torn Vanguard during phase two. DPS should focus on killing those two adds, especially on fortified weeks.","Tank should face the Soul-Torn Vanguard away from the group to avoid players getting stunned by Bonecrushing Strike."]},{name:'Smashspite the Hateful',imgUrl:'smashpite.png',strategy:['Every 25 seconds, the player the furthest away from the boss will be targeted by Hateful Gaze. This causes the boss to charge the target\n          and apply a stack of Hateful Charge. If another player comes inbetween the boss and the target during the charge, the stack is instead\n          applied to that player.','The trick is to spread Hateful charge amongst group members to prevent the boss from charging a player that already has a stack of the debuff,\n          which would effectively one-shot him.','Depending on the level of the keystone and the affixes, the tank may be able to survive a charge from the boss while already having 1 or 2 stacks.','Throughout the fight, party members will randomly be targeted by Fel Vomit. After 6 seconds, a line of acid will be created. If players don\'t\n          position themselves correctly before the 6 seconds expire, it is possible to quickly get overwhelmed by all the acid on the ground.','The trick is to have targets of the Fel Vomit move to the edges of the room to minimize the space taken by the acid lines. While the\n          6 seconds are counting down, a red laser will appear above the player. This allows the target to get a general idea of the direction in which the\n          acid line is going to be created.']},{name:'Lord Kur\'talos Ravencrest',imgUrl:'kurtalos.png',strategy:['During the first phase, players should avoid Whirling Blade (blade that floats from the boss to the targeted player and back.), and tanks should avoid dying to Unerring Shear, which\n          deals a high amount of damage and leaves a stacking debuff (damage over time effect).','At the start of the second phase, when players haven\'t received the Legacy of the Ravencrest buff yet (health, damage, and healing buff), non-tanks are\n          extremely vulnerable to Shadow Bolt Volley. On higher keystones or with Tyrannical affix, this spell will one-shot non-tanks players. It is therefore crucial\n          to pop a defensive cooldown here, as failing to do so will not only kill you, but also prevent you from receving the Legacy of the Ravencrest buff. It is therefore\n          almost always pointless to battle-rez players that died to this first Shadow Bolt Volley.','During phase two, party members will sometimes get affliced with Stinging Swarm, which deals a high amount of damage and stuns the target. All dps should quickly\n          switch their focus to the Stinging Swam and kill it. This becomes even more crucial when Deadlord\'s Guile is about to happen, as the affected player will be stunned\n          and unable to avoid the Dark Obliteration casts.']}]},{name:'Court of Stars',imgUrl:'cos.jpg',bossCount:3,bosses:[{name:'Patrol Captain Gerdo',imgUrl:'gerdo.png',strategy:["Coming soon."]},{name:'Talixae Flamewreath',imgUrl:'talixae.png',strategy:["Coming soon."]},{name:'Advisor Melandrus',imgUrl:'melandrus.png',strategy:["Coming soon."]}]},{name:'Cathedral of Eternal Night',imgUrl:'coen.jpg',bossCount:4,bosses:[{name:'Agronox',imgUrl:'agronox.png',strategy:["Coming soon."]},{name:'Thrashbite the Scornful',imgUrl:'thrashbite.png',strategy:["Coming soon."]},{name:'Domatrax',imgUrl:'domatrax.png',strategy:["Coming soon."]},{name:'Mephistroth',imgUrl:'mephistroth.png',strategy:["Coming soon."]}]},{name:'Darkheart Thicket',imgUrl:'dht.jpg',bossCount:4,bosses:[{name:'Archdruid Glaidalis',imgUrl:'glaidalis.png',strategy:["Coming soon."]},{name:'Oakheart',imgUrl:'oakheart.png',strategy:["Coming soon."]},{name:'Dresaron',imgUrl:'dresaron.png',strategy:["Coming soon."]},{name:'Shade of Xavius',imgUrl:'xavius.png',strategy:["Coming soon."]}]},{name:'Eye of Azshara',imgUrl:'eoa.jpg',bossCount:5,bosses:[{name:'Warlord Parjesh',imgUrl:'parjesh.png',strategy:["Coming soon."]},{name:'Lady Hatecoil',imgUrl:'hatecoil.png',strategy:["Coming soon."]},{name:'Serpentrix',imgUrl:'serpentrix.png',strategy:["Coming soon."]},{name:'King Deepbeard',imgUrl:'deepbeard.png',strategy:["Coming soon."]},{name:'Wrath of Azshara',imgUrl:'azshara.png',strategy:["Coming soon."]}]},{name:'Halls of Valor',imgUrl:'hov.jpg',bossCount:5,bosses:[{name:'Hymdall',imgUrl:'hymdall.png',strategy:["Coming soon."]},{name:'Hyrja',imgUrl:'hyrja.png',strategy:["Coming soon."]},{name:'Fenryr',imgUrl:'fenryr.png',strategy:["Coming soon."]},{name:'God-King Skovald',imgUrl:'skovald.png',strategy:["Coming soon."]},{name:'Odyn',imgUrl:'odyn.png',strategy:["Coming soon."]}]},{name:'Lower Karazhan',imgUrl:'lk.jpg',bossCount:4,bosses:[{name:'Opera Hall',imgUrl:'opera.png',strategy:["Coming soon."]},{name:'Maiden of Virtue',imgUrl:'maiden.png',strategy:["Coming soon."]},{name:'Attumen the Huntsman',imgUrl:'attumen.png',strategy:["Coming soon."]},{name:'Moroes',imgUrl:'moroes.png',strategy:["Coming soon."]}]},{name:'Maw of Souls',imgUrl:'mos.jpg',bossCount:3,bosses:[{name:'Ymiron, the Fallen King',imgUrl:'ymiron.png',strategy:["Coming soon."]},{name:'Harbaron',imgUrl:'harbaron.png',strategy:["Coming soon."]},{name:'Helya',imgUrl:'helya.png',strategy:["Coming soon."]}]},{name:'Neltharion\'s Lair',imgUrl:'nl.jpg',bossCount:4,bosses:[{name:'Rokmora',imgUrl:'rokmora.png',strategy:["Coming soon."]},{name:'Ularogg Cragshaper',imgUrl:'ularogg.png',strategy:["Coming soon."]},{name:'Naraxas',imgUrl:'naraxas.png',strategy:["Coming soon."]},{name:'Dargrul',imgUrl:'dargrul.png',strategy:["Coming soon."]}]},{name:'Upper Karazhan',imgUrl:'lk.jpg',bossCount:4,bosses:[{name:'The Curator',imgUrl:'curator.png',strategy:["Coming soon."]},{name:'Shade of Medivh',imgUrl:'medivh.png',strategy:["Coming soon."]},{name:'Mana Devourer',imgUrl:'devourer.png',strategy:["Coming soon."]},{name:'Viz\'aduum the Watcher',imgUrl:'vizaduum.png',strategy:["Coming soon."]}]},{name:'Seat of the Triumvirate',imgUrl:'sott.jpg',bossCount:4,bosses:[{name:'Zuraal the Ascended',imgUrl:'zuraal.png',strategy:["Coming soon."]},{name:'Saprish',imgUrl:'saprish.png',strategy:["Coming soon."]},{name:'Viceroy Nezhar',imgUrl:'nezhar.png',strategy:["Coming soon."]},{name:'L\'ura',imgUrl:'lura.png',strategy:["Coming soon."]}]},{name:'The Arcway',imgUrl:'arc.jpg',bossCount:5,bosses:[{name:'Ivanyr',imgUrl:'ivanyr.png',strategy:["Coming soon."]},{name:'Corstilax',imgUrl:'corstilax.png',strategy:["Coming soon."]},{name:'Nal\'tira',imgUrl:'naltira.png',strategy:["Coming soon."]},{name:'General Xakal',imgUrl:'xakal.png',strategy:["Coming soon."]},{name:'Advisor Vandros',imgUrl:'vandros.png',strategy:["Coming soon."]}]},{name:'Vault of the Wardens',imgUrl:'votw.jpg',bossCount:5,bosses:[{name:'Tirathon Saltheril',imgUrl:'tirathon.png',strategy:["Coming soon."]},{name:'Inquisitor Tormentorum',imgUrl:'tormentorum.png',strategy:["Coming soon."]},{name:'Glazer',imgUrl:'glazer.png',strategy:["Coming soon."]},{name:'Ash\'Golm',imgUrl:'ashgolm.png',strategy:["Coming soon."]},{name:'Cordana Felsong',imgUrl:'vandros.png',strategy:["Coming soon."]}]}];

